@font-face {
  font-family: 'texicab';
  src:  url('../fonts/texicab.eot?2s3jwd');
  src:  url('../fonts/texicab.eot?2s3jwd#iefix') format('embedded-opentype'),
    url('../fonts/texicab.ttf?2s3jwd') format('truetype'),
    url('../fonts/texicab.woff?2s3jwd') format('woff'),
    url('../fonts/texicab.svg?2s3jwd#texicab') format('svg');
  font-weight: normal;
  font-style: normal;
}

.tc {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'texicab' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

